* {
  font-size: 18px;
  font-family: monospace;
}
body {
    background-color: black;
}

.App {
    width: 320px;
    margin: 0 auto;
    padding: 8px;
    border: 4px solid;
    color: #0f0;
    min-height: 141px;
}

.btnContainer {
    display: flex;
    justify-content: space-between;
}

.clearHistoryBtn {
    color: red;
}

.heroIcon {
    height: 128px;
    display: block;
    margin: 0 auto;
    border: 4px solid white;
}

.heroesHistory li:first-of-type {
    text-decoration: underline;
    font-size: 30px;
}

.heroesHistory li:not(:first-of-type) {
    opacity: .75;
}
